import React from 'react'
import { graphql } from 'gatsby'
import { Container } from 'theme-ui'
import Layout from '@solid-ui-layout/Layout'
import Seo from '@solid-ui-components/Seo'
import Divider from '@solid-ui-components/Divider'
import Header from '@solid-ui-blocks/Home/HeaderBlock01'
import Hero from '@solid-ui-blocks/Home/HeroBlock01'
import Hero2 from '@solid-ui-blocks/Home/HeroBlock02'
import Team from '@solid-ui-blocks/Home/HeroBlock03'
import Products from '@solid-ui-blocks/Home/CompaniesBlock01'
import Companies2 from '@solid-ui-blocks/Home/CompaniesBlock03'
import WhyChooseUs1 from '@solid-ui-blocks/Home/FeaturesBlock01'
import FeatureThreeMore from '@solid-ui-blocks/Home/FeaturesBlock07'
import FeatureThree from '@solid-ui-blocks/Home/FeaturesWithPhotoBlock03'
import BuildBrand from '@solid-ui-blocks/Home/FeaturesWithPhotoBlock04'
import Stats from '@solid-ui-blocks/Home/StatsBlock01'
import Buttons from '@solid-ui-blocks/Home/ButtonsBlock01'
import Testimonials from '@solid-ui-blocks/Home/TestimonialsBlock03'
import Blog2 from '@solid-ui-blocks/Home/BlogBlock02'
import Footer from '@solid-ui-blocks/Home/FooterBlock01'
import { normalizeBlockContentNodes } from '@blocks-helpers'
import { Helmet } from "react-helmet"
import theme from './_theme'
import styles from './_styles'
import './home.module.css'

const HomePage = props => {
  const { allBlockContent } = props.data
    const content = normalizeBlockContentNodes(allBlockContent?.nodes)

return (
  <Layout theme={theme} {...props}>
    <Seo title='Marketing Automation & Customer Engagement Tools' />
      {/* Blocks */}
      <Header content={content['header']} />

      <Container variant='full' sx={styles.heroContainer}>
        <Hero content={content['hero']} reverse />
      </Container>
      
      <Divider space='2' />
      <Container variant='full' sx={styles.featureThreeContainer}>
        <FeatureThreeMore content={content['feature-three-more']} />
          <Divider space='4' />
        <FeatureThree content={content['feature-three']} />  
      </Container>
      <Divider space='4' />

      <Container variant='wide' sx={styles.whyChooseUsContainer}>
        <WhyChooseUs1 content={content['why-choose-us2']} />
      </Container>
      <Buttons content={content['buttons']} />
      <Divider space='6' />

      <Container variant='full' sx={styles.heroContainer2Main}>
        <Container variant='full' sx={styles.heroContainer2}>
          <Divider space='2' />
          <Hero2 content={content['hero2']} />
        </Container>
      </Container>
        
      <Divider space='4' />
      <Container variant='full' sx={styles.buildBrandContainer}>
        <BuildBrand content={content['build-brand']} />
      </Container>
      <Divider space='5' />
      <Stats content={content['get-started']} />
      <Divider space='5' />

      <Divider space='5' />
      <Products content={content['companies']} />
      <Divider space='3' />

      <Divider space='5' />
      <Container variant='wide' sx={styles.testimonialsContainer}>
        <Testimonials content={content['testimonials']} />
        <Divider space='5' color='omegaLight' />
        <Companies2 content={content['companies2']} />
      </Container>
      <Divider space='5' />
      <Blog2 />
      <Divider space='5' />
      
      <Team content={content['team']} />
      <Footer content={content['footer']} />

      <Helmet>
        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org",
              "@type": "Organization",
              "name": "Mumara",
              "alternateName": "Marketing Automation &amp; Customer Engagement Tools | Mumara",
              "url": "https://www.mumara.com",
              "logo": "https://www.mumara.com/wp-content/uploads/2018/02/logo.png",
              "contactPoint": {
                "@type": "ContactPoint",
                "telephone": "+1 (844) 686 – 2721",
                "contactType": "customer service"
              },
              "sameAs": [
                "https://www.facebook.com/MumaraApps",
                "https://twitter.com/MumaraApps",
                "https://plus.google.com/+MumaraEmailMarketer",
                "https://www.youtube.com/channel/UC1k6voQOVss0_PWg7CfJTdw",
                "https://www.linkedin.com/company/mumara"
              ]
            }
          `}
        </script>
        <script type="application/ld+json">
          {`
            {
              "@context":"https://schema.org",
              "@type":"ItemList",
              "itemListElement":[
                {
                  "@type":"ListItem",
                  "position":1,
                  "url":"https://www.mumara.com/campaigns/"
                },
                {
                  "@type":"ListItem",
                  "position":2,
                  "url":"https://www.mumara.com/one/"
                },
                {
                  "@type":"ListItem",
                  "position":3,
                  "url":"https://www.mumara.com/sms/"
                },
                {
                  "@type":"ListItem",
                  "position":4,
                  "url":"https://www.mumara.com/smsplus/"
                },
                {
                  "@type":"ListItem",
                  "position":5,
                  "url":"https://www.mumara.com/classic/"
                }
              ]
            }
          `}
        </script>

        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org",
              "@type": "Person",
              "name": "Mumara",
              "url": "https://www.mumara.com",
              "sameAs": [
                "https://www.facebook.com/MumaraApps",
                "https://twitter.com/MumaraApps",
                "https://plus.google.com/+MumaraEmailMarketer",
                "https://www.youtube.com/channel/UC1k6voQOVss0_PWg7CfJTdw",
                "https://www.linkedin.com/company/mumara"
              ]
            }
          `}
        </script>

        <script type="application/ld+json">
          {`
            {
              "@context": "https://schema.org/",
              "@type": "WebSite",
              "name": "Mumara",
              "url": "https://www.mumara.com/",
              "potentialAction": {
                "@type": "SearchAction",
                "target": "https://www.mumara.com/?s={s}",
                "query-input": "required name=s"
              }
            }
          `}
        </script>
        <script src="https://code.jquery.com/jquery-3.6.0.min.js" integrity="sha256-/xUj+3OJU5yExlq6GSYGSHk7tPXikynS7ogEvDej/m4=" crossorigin="anonymous"></script>
        <script data-ad-client="ca-pub-7570269198350551" async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
        <script id='sbinit' src='https://dashboard.mumara.com/js/init.js'></script>
        <script>
          {`
            window.ga=window.ga||function(){(ga.q=ga.q||[]).push(arguments)};ga.l=+new Date;
            ga('create', 'UA-63566119-1', 'auto');
            ga('send', 'pageview');
          `}
        </script>
        <script async src='https://www.google-analytics.com/analytics.js'></script>
      </Helmet>

  </Layout>
)
}

export const query = graphql`
  query indexBlockContent {
    allBlockContent(filter: { page: { in: ["main"] } }) {
      nodes {
        ...BlockContent
      }
    }
  }
`

export default HomePage
